import React from 'react';
import { Card } from './index'

function Days() {

	const tabs = [
		{
			value: ' '
		},
		{
			value: ' '
		},
		{
			value: ' 10 '
		}
	]

	const days = [
		{
			day: '',
			dayInfo: '28 ',
			iconPath: '',
			tempDay: '+18',
			tempNight: '+15',
			info: ''
		},
		{
			day: '',
			dayInfo: '29 ',
			iconPath: '',
			tempDay: '+15',
			tempNight: '+10',
			info: ' '
		},
		{
			day: '',
			dayInfo: '28 ',
			iconPath: '',
			tempDay: '+18',
			tempNight: '+15',
			info: ''
		},
		{
			day: '',
			dayInfo: '28 ',
			iconPath: '',
			tempDay: '+18',
			tempNight: '+15',
			info: ''
		},
		{
			day: '',
			dayInfo: '28 ',
			iconPath: '',
			tempDay: '+18',
			tempNight: '+15',
			info: ''
		},
		{
			day: '',
			dayInfo: '28 ',
			iconPath: '',
			tempDay: '+18',
			tempNight: '+15',
			info: ''
		},
		{
			day: '',
			dayInfo: '28 ',
			iconPath: '',
			tempDay: '+18',
			tempNight: '+15',
			info: ''
		},
	]

	return (
		<div className='days'>
			<div className="days__filter filter-days">
				<div className='filter-days__buttons'>
					{tabs.map((obj) => (
						<button className='filter-days__button'
							type='button'
							key={obj.value}
						>
							{obj.value}
						</button>
					))}
				</div>
			</div>
			<div className='days__content'>
				{days.map((obj) => (
					<Card key={`${obj.day}_${obj.dayInfo}`} day={obj} />
				))}
			</div>
		</div>
	)
}

export default Days;